import React, {useState, useEffect} from 'react'

export default function WorkedFor() {

    const [jobs, setJobs] = useState([]);

    async function fetchtheAPI() {
        const url = "https://gauravghati.github.io/apis/workedfor.json";
        const response = await fetch(url);
        const jsondata = await response.json();
        console.log(jsondata);
        setJobs(jsondata);
    }

    useEffect(() => {
        fetchtheAPI();
    }, []);


    if(!jobs.internships) return (<h> Loading.... </h>);
    if(!jobs.volunteer) return (<h> Loading.... </h>);

    return (
        <div class="certifi divCertitwo">
            <b class="fontSizeWorkedFor">• Internships</b>
            <table>
                {
                    jobs.internships.map((intern) => {
                        return <tbody>
                            <tr></tr>
                            <tr></tr>
                            <tr>
                                <td class="c-heading">{intern.date}</td>
                            </tr>
                            <tr>
                                <td class="c-main">{intern.position}</td>
                            </tr>
                            <tr>
                                <td class="c-des certiTr">{intern.place}</td>
                            </tr>
                            <tr>
                                <td class="c-des certiTrBtn" colspan="2">{intern.description}</td>
                            </tr>					
                        </tbody>    
                    })
                }
            </table>

            <b class="fontSizeWorkedFor">• Volunteer</b>
            <table>
                {
                    jobs.volunteer.map((vol) => {
                        return <tbody>
                            <tr></tr>            
                            <tr></tr>
                            <tr>
                                <td class="c-heading">{vol.date}</td>
                            </tr>
                            <tr>
                                <td class="c-main">{vol.heading}</td>
                            </tr>
                            <tr>
                                <td class="c-des certiTrBtn" colspan="2">{vol.description}</td>
                            </tr>
                        </tbody>
                    })
                }
            </table>
        </div>
    );
}
