import React, {useState, useEffect} from 'react'

function openTab(th) {
    window.open(th,'_blank');
}

export default function OpenSource() {

    const [open, setOpenSource] = useState([]);

    async function fetchtheAPI() {
        const url = "https://gauravghati.github.io/apis/opensource.json";
        const response = await fetch(url);
        const jsondata = await response.json();
        setOpenSource(jsondata);
    }

    useEffect(() => {
        fetchtheAPI();
    }, []);

    if(!open.prs) return (<h> Loading.... </h>);
    if(!open.issues) return (<h> Loading.... </h>);

    console.log(open.prs);
    console.log(open.issues); 

    return (
        <div class="certifi divCertitwo">
            <b class="fontSizeWorkedFor">• Merged Pull Requests</b>
            <table>
                {
                    open.prs.map((pr) => {
                        return <tbody>
                            <tr></tr>
                            <tr>
                                <td class="c-heading">{pr.date}</td>
                            </tr>
                            <tr>
                                <td class="c-main">{pr.title}</td>
                                <td class="tdr"><button class="btn-certi" onClick={openTab.bind(this, pr.prlink)} name={pr.prlink}>PRLink</button></td>
                            </tr>
                            <tr>
                                <td class="c-des certiTr">Repo of: {pr.reponame}</td>
                            </tr>
                            <tr>
                                <td class="c-des certiTrBtn" colspan="2">Field: {pr.field}</td>
                            </tr>
                        </tbody>
                    })
                }
            </table>

            <b class="fontSizeWorkedFor">• Issues Resolved</b>
            <table>  
                {
                    open.issues.map((iss) => {
                        return <tbody>
                            <tr></tr>
                            <tr>
                                <td class="c-heading">{iss.date}</td>
                            </tr>
                            <tr>
                                <td class="c-main">{iss.title}</td>
                                <td class="tdr"><button class="btn-certi" onclick={openTab.bind(this, iss.issuelink)} name={iss.issuelink}>IssueLink</button></td>
                            </tr>
                            <tr>
                                <td class="c-des certiTr">Repo: {iss.reponame}</td>
                            </tr>
                            <tr>
                                <td class="c-des certiTrBtn" colspan="2">Field: {iss.field}</td>
                            </tr>
                        </tbody>    
                    })
                }
            </table>
        </div>
    );
}
